/* Module: S */
.articlewrapper, .eventwrapper {
    margin-top: -68px;
    transition: all 0.5s ease-in-out;
}
.back_arrow {
    margin: 13px 0px 0px 35px;
    background-color: $white;
    -webkit-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
    display: inline-block;
    padding: 3px 10px;
    height: 30px;
    position: relative;
    a {
        font-size: pxToRem(14);
        line-height: pxToRem(25);
        color: $base-font-colour;
        text-decoration: none;
    }
    &:before {
        content: '';
        position: absolute;
        transform: rotate(45deg);
        width: 20px;
        height: 20px;
        left: -10px;
        top: 5px;
        z-index: -1;
        -webkit-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
    }
    &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        left: -15px;
        top: 0px;
        border-style: solid;
        border-width: 15px 15px 15px 0;
        border-color: transparent #ffffff transparent transparent;

    }
}

.main_article {
    margin: 15px;
    padding: 15px;
    background-color: $white;
    h1, h2, h3, h4 {
        margin-top: 0px;
        a {
            color: $base-font-colour;
            text-decoration: none;
        }
    }
    p {
        a {
            color: $base-font-colour;
            text-decoration: none;
        }
    }
    .introimage {
        width: 100%;
        height: 0;
        padding-bottom: 146px;
        overflow: hidden;
        margin: 0px;
        margin-bottom: 14px;
        position: relative;
        .image {
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            top: 0px;
            left: 0px;
            bottom: 0px;
            right: 0px;
        }
    }
    .button {
        font-size: pxToRem(10);
        line-height: pxToRem(20);
        padding: 0.6em 1.6em;
        min-width: 156px;
        text-align: center;
        @media screen and (min-width: 321px) {
            font-size: pxToRem(12);
            line-height: pxToRem(16);
            min-width: 178px;
        }
        background-color: $gray-darkest;
        &:focus, &:hover {
            background-color: $gray-darkest;
            color: $white;
            text-decoration: none;
        }
        a {
            color: $white;
        }
    }
    .socialicons {
        margin-bottom: 14px;
        .button {
            font-size: pxToRem(10);
            line-height: pxToRem(20);
            padding: 0.6em 1.6em;
            min-width: 156px;
            text-align: center;
            @media screen and (min-width: 321px) {
                font-size: pxToRem(12);
                line-height: pxToRem(16);
                min-width: 178px;
            }
            background-color: $gray-darkest;
            height: 30px;
            margin: 0;
            &:focus, &:hover {
                background-color: $gray-darkest;
                color: $white;
                text-decoration: none;
            }
            &.active {
                background-color: $green;
                color: $base-font-colour;
                position: relative;
                &:before {
                    display: block;
                    content: "";
                    position: absolute;
                    width: 5px;
                    height: 10px;
                    left: 10px;
                    top: 8px;
                    transform: rotate(45deg);
                    border-style: solid;
                    border-color: transparent black black transparent;
                    border-width: 1px;
                }
            }
        }
        ul.share {
            padding: 0;
            margin: 0;
            float: right;
            li {
                display: inline-block;
                a {
                    width: 30px;
                    height: 30px;
                    border-radius: 15px;
                    display: block;
                    &.twitter {
                        background: $gray-darkest url({{file_dest}}/twitter-icon.svg) center center / 13px 13px no-repeat;
                    }
                    &.facebook {
                        background: $gray-darkest url({{file_dest}}/facebook-icon.svg) center center / 13px 13px no-repeat;
                    }
                    &.linkedin {
                        background: $gray-darkest url({{file_dest}}/linkedin-icon.svg) center center / 13px 13px no-repeat;
                    }
                }

            }
        }
    }
}